import React, { Component } from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import logo from '../images/logo.png';
import userImage from '../images/Angelo.jpg';

import CreateAnime from './create'
import EditAnime from './edit'
import DeleteAnime from './delete'

import Modal from './utils/modal';

export default class Admin extends Component {

    constructor() {
        super()
        this.renderForm = this.renderForm.bind(this);
        this.state = {
            isShowing: false,
            modalType: ''
        }
    }

    openModalHandler = () => {
        this.setState({
            isShowing: true
        });

        document.body.style.overflow = 'hidden'

        const modal = document.getElementsByClassName('modal')

        for (let i = 0; i < modal.length; i++) {
            modal[i].style.display= 'inherit'
            
        }
    }

    closeModalHandler = () => {
        this.setState({
            isShowing: false
        });

        const modal = document.getElementsByClassName('modal')

        for (let i = 0; i < modal.length; i++) {
            modal[i].style.display= 'none'
            
        }

        document.body.style.overflow = 'initial'

        document.getElementById('create-anime-form').style.display = 'none'
        document.getElementById('App-search').style.display = 'block'
    }

    renderForm(type) {

        let node;

        switch(type) {

            case 'Crear':
                node = <CreateAnime />
                break
            case 'Editar':
                node = <EditAnime />
                break
            case 'Eliminar':
                node = <DeleteAnime />
                break
            default:
                node = ''
        }

        this.setState({
            formType: node,
            modalType: type
        }, this.openModalHandler())
    }

    render(){
        
        return(
            
            <div className="App-header Admin-panel">
                { this.state.isShowing ? <div onClick={this.closeModalHandler} className="back-drop"></div> : null }
                <p className="Admin-panel-title">Admin panel</p>
                <nav>
                    <ul>
                        <a className="App-link open-modal-btn" onClick={e => {this.renderForm('Crear')}}>
                        <FontAwesomeIcon icon="plus-square" />
                        </a>
                        <a className="App-link open-modal-btn" onClick={e => {this.renderForm('Editar')}}><FontAwesomeIcon icon="edit" /></a>
                        <a className="App-link open-modal-btn" onClick={e => {this.renderForm('Eliminar')}}><FontAwesomeIcon icon="trash-alt" /></a>
                    </ul>
                    <p className="Admin-panel-name">
                    <span>Bienvenido, Anghelo</span> <img src={userImage} alt="Usuario" />
                    </p>
                </nav>
                <Modal
                    formType={this.state.formType}
                    modalType={this.state.modalType}
                    show={this.state.isShowing}
                    close={this.closeModalHandler}>
                        Maybe aircrafts fly very high because they don't want to be seen in plane sight?
                </Modal>    
            </div>

        );
    }
}