import React, { Component } from 'react';

import defaultAnimeImage from '../images/default.png';

import Search from './search';

import firebase from './utils/firebase';

export default class EditAnime extends Component {

    showEditImagePreview(event) {
        document.getElementById('create-anime-thumbnail').src = URL.createObjectURL(event.target.files[0])
    }

    EditSong() {

        const songsRef = firebase.database().ref().child('songs'),
            storage = firebase.storage().ref().child('img')

        let name = document.getElementById('create-anime-text').value,
            type = document.getElementById('create-anime-select').value,
            number = document.getElementById('create-anime-number').value,
            key = document.getElementById('create-anime-hidden').value,
            image = document.getElementById('create-anime-image'),
            itemLoaderCounter = document.getElementById('item-loader-counter'),
            form = document.getElementById('create-anime-form'),
            itemLoader = document.getElementById('item-loader')


        try{
            if(image.files.length){
                try {
            
                let file = image.files[0],
                    fileRef = storage.child(file.name),
                    uploadImg = fileRef.put(file)
            
                uploadImg.on('state_changed', function(snapshot){
                    // Observe state change events such as progress, pause, and resume
                    // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
                    var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                    itemLoaderCounter.innerText = 'Editando canción...  '+ progress+'%'
            
                    }, function(error) {
                    itemLoader.style.display = 'none'
                    }, function() {
                    // Handle successful uploads on complete
                    uploadImg.snapshot.ref.getDownloadURL().then( url => {
            
                        let id = key,
                            songData = {
                                name: name,
                                type: type,
                                number: number,
                                image: url
                            },
                            updateSong = {}
                    
                        updateSong[`/${id}`] = songData
                        
                        songsRef.update(updateSong)
                        form.reset()
                        document.getElementById('create-anime-form').style.display = 'none'
                        document.getElementById('App-search').style.display = 'initial'
            
                    });
                    });
            
                }catch(err){
                    itemLoader.innerText = 'Error en subida de archivo'
                }
    
            }else{
                
                try{

                    firebase.database().ref().child('/songs/' + key).update({
                        name: name,
                        type: type,
                        number: number,
                    })
                }catch(err){
                    console.log('Hay un error')
                }
                
                itemLoader.style.display = 'flex'
                itemLoader.innerText = '✓ Correción exitosa'
    
            }

            
        }
        catch(err){
            itemLoader.innerText += 'Oops, algo salió mal. Inténtalo luego'
        }

        console.log('Canción editada')

    }

    render(){   
        
        return(
            <div id="Edit-component" className="Create-anime-component">
                <Search fromModalType='Editar' />
                <form className="Create-anime-form" id="create-anime-form">
                    <img id="create-anime-thumbnail" />
                    <label for="create-anime-image">Editar imagen</label>
                    <input onChange={e => {this.showEditImagePreview(e)}}id="create-anime-image" type='file' accept='image/x-png,image/gif,image/jpeg'/>
                    <input id="create-anime-text" type='text' placeholder='Nombre del Animu'/>
                    <select id="create-anime-select">
                        <option>Opening</option>
                        <option>Ending</option>
                    </select>
                    <input id="create-anime-number" type='number' placeholder='#' />
                    <input id="create-anime-hidden" type="hidden" />
                    <input onClick={e => {e.preventDefault();this.EditSong()}} type="submit" value="Confirmar"/>
                    <div id="item-loader">
                        <span id="item-loader-counter"></span>
                    </div>
                </form>
            </div>
        );
    }
}