import React, { Component } from 'react';

import defaultAnimeImage from '../images/default.png';

import Search from './search';

import firebase from './utils/firebase';

export default class DeleteAnime extends Component {

    deleteSong() {

        const itemLoader = document.getElementById('item-loader'),
            key = document.getElementById('create-anime-hidden').value

        firebase.database().ref().child('/songs/' + key).remove()

        itemLoader.style.display = 'flex'
        itemLoader.innerText = '✓ Canción eliminada con éxito'
    }

    render(){
        return(
            <div id="Edit-component" className="Create-anime-component">
                <Search fromModalType='Eliminar' />
                <form className="Create-anime-form" id="create-anime-form">
                    <div id="create-anime-component-item">
                        <p id="delete-question">¿Seguro que quieres borrar está canción?</p>
                        <img id="create-anime-thumbnail" />
                        <div>
                            <p id="create-anime-text"></p>
                            <span id="create-anime-type"></span>
                            <span id="create-anime-number"></span>
                        </div>
                    </div>
                    <input id="create-anime-hidden" type="hidden" />
                    <div id="create-anime-form-buttons">
                        <input type="button" value="Ahora no, joven"/>
                        <input onClick={e => {e.preventDefault();this.deleteSong()}} type="submit" value="Sí papu"/>
                    </div>
                    <div id="item-loader">
                        <span id="item-loader-counter"></span>
                    </div>
                </form>
            </div>
        );
    }
}