import React, { Component } from 'react';

import defaultAnimeImage from '../images/default.png';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import Anime from './anime';

import firebase from './utils/firebase';

export default class Search extends Component {

    constructor() {
        super();
        //this.updateMessage = this.updateMessage.bind(this);
        //this.submitMessage = this.submitMessage.bind(this);
        this.searchSong = this.searchSong.bind(this);
        this.getSongInfo = this.getSongInfo.bind(this);
        this.getInputValue = this.getInputValue.bind(this);
        this.state = {
            songs: [],
            inputValue: ''
        }
    }

    getInputValue(e) {

        this.setState({
            inputValue: e.target.value
        })

        this.searchSong(e)
    }

    searchSong(e) {

        let songsArray = []

        const text = this.state.inputValue

        if(e) {

            firebase.database().ref('songs/').orderByChild('name').startAt(text).limitToFirst(10).on('value', songList => {
                songsArray = []
                songList.forEach(function(snap) {
                    let song = snap.val()
                    let key = snap.key
    
                    songsArray.push({
                        id: key,
                        anime: song.name,
                        type: `${song.type} ${song.number}`,
                        image: song.image,
                        date: song.date,
                        inList: song.inList
                    })
                })
    
                this.setState({
                    songs: songsArray
                })
                
            })

        }
        
    }

    addSongToList(id) {

        firebase.database().ref('songs/' + id).update({
            inList: true,
            date: Date.now()
        })
    }

    getSongInfo(id, modalType) {


        firebase.database().ref('songs/' + id).once("value", function(snapshot) {
            let key = snapshot.key;
            let song = snapshot.val();

            if(modalType == 'Editar') {

                document.getElementById('create-anime-text').value = song.name
                document.getElementById('create-anime-select').value = song.type        
                document.getElementById('create-anime-number').value = song.number
            }

            if(modalType == 'Eliminar') {
                document.getElementById('create-anime-text').textContent = song.name
                document.getElementById('create-anime-type').textContent = song.type        
                document.getElementById('create-anime-number').textContent = ' ' + song.number
            }
            
            document.getElementById('create-anime-thumbnail').src = song.image
            document.getElementById('create-anime-form').style.display = 'flex'
            document.getElementById('App-search').style.display = 'none'
            document.getElementById('create-anime-hidden').value = key
            
            
          });
    }

    handleSubmit = (e) => {

        e.preventDefault(); // Let's stop this event.
        e.stopPropagation(); // Really this time.

    }

    render(){

        let message = '',
            styles = {
                display: 'none',
                height: '100%',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                padding: '3rem',
                fontWeight: 100

            };

        console.log(this.state.songs[0],this.state.songs[1])

        if(this.state.songs[0] == undefined){
            
            message = 'Las mejores canciones anime en un solo lugar'
            styles.display = 'flex'

        }else {
            message = ''
            styles.display = 'none'
        }

        return(
            <section id="App-search" className="App-search">
                <div className="App-search-header">
                    <form onSubmit={this.handleSubmit} >
                        <input id="search-song" onChange={e => this.getInputValue(e)} type="text" placeholder={this.props.fromModalType ? "Buscar canción..." : "Pide una canción..."} />
                    </form>
                </div>
                <div id="container2" className="App-search-body container">
                    { 
                        this.state.songs.map( (song,i) => {
                            return <div onClick={e => {if(this.props.fromModalType){this.getSongInfo(song.id,this.props.fromModalType)}else{this.addSongToList(song.id)}}}><Anime songInList={song.inList} key={i} imageSrc={song.image} animeName={song.anime} songName={song.type} /></div>
                        })
                    }
                    <div className="App-search-message" style={styles}>
                        <p>{message}</p>
                        <FontAwesomeIcon icon="headphones-alt" />
                    </div>
                </div>
            </section>

        );
    }
}